@import "https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&family=Spline+Sans+Mono&display=swap";
:root {
  --font-family: "Roboto Slab", sans-serif;
  --code-font-family: "Spline Sans Mono", monospace;
  --font-size: 16px;
  --text-color: #222;
  --brand-color: #088358;
  --background: #fafafa;
  --nav-bg: #efefeed2;
  --bar-color: #dae3df;
  --focus-color: #08835880;
  --brand-text-color: #ecf3dd;
  --code-bg: #f8ffe9;
  --spacing: 10px;
  --line-height: calc(var(--font-size) * 1.6);
  --h1-scale: 2;
  --h2-scale: 1.5;
  --h3-scale: 1.25;
  --tab-selector-selected-color: var(--brand-color);
  --tab-selector-bar-color: var(--brand-text-color);
  --touch-size: 32px;
  --header-height: calc(var(--line-height) * var(--h2-scale)  + var(--spacing) * 2);
}

* {
  box-sizing: border-box;
  color: var(--text-color);
}

body {
  font-family: var(--font-family);
  font-size: var(--font-size);
  line-height: var(--line-height);
  background: var(--background);
  margin: 0;
}

input, button, select, textarea {
  font-family: var(--font-family);
  font-size: var(--font-size);
}

select {
  -webkit-appearance: none;
}

header {
  background: var(--brand-color);
  color: var(--brand-text-color);
  --text-color: var(--brand-text-color);
  padding: 0 calc(var(--spacing) * 1.5);
  line-height: calc(var(--line-height) * var(--h1-scale));
  height: var(--header-height);
  white-space: nowrap;
  align-items: center;
  display: flex;
}

h1 {
  --text-color: var(--brand-color);
  font-size: calc(var(--font-size) * var(--h1-scale));
  line-height: calc(var(--line-height) * var(--h1-scale));
  padding: var(--spacing);
  text-align: center;
  margin: 0;
  font-weight: 400;
}

header h2 {
  color: var(--brand-text-color);
  white-space: nowrap;
}

h2 {
  color: var(--brand-color);
  font-size: calc(var(--font-size) * var(--h2-scale));
  line-height: calc(var(--line-height) * var(--h2-scale));
  margin: calc(var(--spacing) * var(--h2-scale)) 0;
}

h3 {
  font-size: calc(var(--font-size) * var(--h3-scale));
  line-height: calc(var(--line-height) * var(--h3-scale));
  margin: calc(var(--spacing) * var(--h3-scale)) 0;
}

main {
  flex-direction: column;
  align-items: stretch;
  max-width: 100vw;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

main > side-nav {
  height: calc(100vh - var(--header-height));
}

.bar {
  gap: var(--spacing);
  padding: var(--spacing);
  --text-color: var(--brand-color);
  background: var(--bar-color);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

a {
  border-bottom: 1px solid var(--brand-color);
  text-decoration: none;
}

a:hover {
  color: var(--brand-color);
}

button, select, .clickable {
  background: var(--brand-text-color);
  --text-color: var(--brand-color);
  padding: 0 calc(var(--spacing) * 1.25);
  border-radius: calc(var(--spacing) * .5);
  border: none;
  text-decoration: none;
  transition: all .2s ease-out;
  display: inline-block;
}

button, select, clickable, input {
  line-height: calc(var(--line-height)  + var(--spacing));
}

select:has( + .icon-chevron-down) {
  padding-right: calc(var(--spacing) * 3);
}

select + .icon-chevron-down {
  margin-left: calc(var(--spacing) * -2);
  min-width: calc(var(--spacing) * 2.2);
  pointer-events: none;
  color: var(--brand-color);
  align-self: center;
}

label > select + .icon-chevron-down {
  margin-left: calc(var(--spacing) * -2.5);
}

input {
  padding: 0 calc(var(--spacing) * 1.5);
  border-radius: calc(var(--spacing) * .5);
  border: none;
  outline: none;
}

:focus {
  box-shadow: inset 0 0 0 2px var(--focus-color);
}

::placeholder {
  color: var(--focus-color);
}

img {
  vertical-align: middle;
}

button:hover, button:hover, .clickable:hover {
  box-shadow: inset 0 0 0 2px var(--brand-color);
}

button:active, button:active, .clickable:active {
  background: var(--brand-color);
  color: var(--brand-text-color);
}

input[type="number"] {
  text-align: right;
  width: 6em;
}

label {
  gap: calc(var(--spacing) * .5);
  align-items: center;
  display: inline-flex;
}

.elastic {
  flex: auto;
  position: relative;
  overflow: hidden;
}

svg text {
  pointer-events: none;
  fill: #000;
  stroke: #fff8;
  stroke-width: .5px;
  opacity: 0;
  font-size: 16px;
  font-weight: bold;
}

svg text.hover {
  opacity: 1;
}

.thead {
  background: var(--brand-color);
  --text-color: var(--brand-text-color);
}

.th + .th {
  border: 0 solid #fff4;
  border-width: 0 1px;
}

.th, .td {
  padding: 0 var(--spacing);
}

.tr:hover {
  background: #08835810;
}

:disabled {
  opacity: .5;
  filter: saturate(0);
  pointer-events: none;
}

pre {
  background: var(--code-bg);
  padding: var(--spacing);
  border-radius: calc(var(--spacing) * .25);
  font-size: 75%;
  line-height: calc(var(--font-size) * 1.2);
  overflow: scroll;
}

pre, code {
  font-family: var(--code-font-family);
  --text-color: var(--brand-color);
}

.-side-nav-visible .close-content {
  display: none;
}

.transparent, .iconic {
  background: none;
}

.iconic {
  font-size: 150%;
  line-height: calc(var(--line-height)  + var(--spacing));
  width: calc(var(--line-height)  + var(--spacing));
  text-align: center;
  padding: 0;
}

.transparent:hover, .iconic:hover {
  box-shadow: none;
  color: var(--text-color);
  background: #0002;
}

.transparent:active, .iconic:active {
  box-shadow: none;
  color: var(--text-color);
  background: #0004;
}

side-nav:not([compact]) .show-within-compact {
  display: none;
}

.on.on {
  background: var(--brand-color);
  --text-color: var(--brand-text-color);
}

rich-text [part="toolbar"] {
  --text-color: var(--brand-color);
}

rich-text [part="doc"] {
  border-top: .25px solid var(--brand-color);
  padding: 20px;
  box-shadow: none !important;
}

.current {
  color: var(--brand-color);
  background: var(--background);
}

.doc-link {
  cursor: pointer;
  padding: calc(var(--spacing) * .5) calc(var(--spacing) * 1.5);
  border-bottom: none;
}

.doc-link:not(.current) {
  opacity: .75;
}

live-example {
  margin: var(--spacing) 0;
}

.icon-plus {
  content: "+";
}

